<template>
  <div
    class="mb-10 text-center md:text-left rounded-3xl md:rounded-none p-14 md:p-0 md:pe-10 bg-gray-50 md:bg-transparent"
  >
    <img
      class="inline object-cover w-36 h-36 mb-2.5 rounded-full"
      :src="user.avatar"
      alt="Profile image"
    />
    <h2 class="font-bold text-base text-dark">{{ user.name }}</h2>
    <p class="text-light-gray text-sm mb-2 font-light">
      {{ user.description }}
    </p>
    <div class="text-sm">
      <p>Joining date</p>
      <p class="text-light-gray font-light">{{ user.joiningDate }}</p>
    </div>
    <!-- user apps -->
    <ul class="list-none mt-9">
      <li v-for="item in user.links" :key="`link-${item.id}`">
        <button
