          { id: 12, text: "Apps" },
          { id: 13, text: "Blogs" },
        ],
      },
      currentCat: { id: 0, text: "All Apps" },
    };
  },

  methods:{
      itemClicked(item){
          this.currentCat = item
         this.$emit('itemClicked', item)
      }
  }
};
</script>

