import Vue from 'vue';
import Router from 'vue-router';
import NProgress from 'nprogress';

import Home from '../components/views/Home.vue';
import Categories from '../components/views/Categories.vue';
import Themes from '../components/views/Themes.vue';
import AppShow from '../components/views/AppShow.vue';
import User from '../components/views/User.vue';

Vue.use(Router);

const routes = [{
        path: '/',
        name: 'home',
        component: Home,
        meta: {
            bodyClas: 'vue--home',
        }
    },
    {
        path: '/categories',
        name: 'categories',
        component: Categories,
        meta: {
            bodyClas: 'vue--categories',
        }
    }, {
        path: '/user',
        name: 'user',
        component: User,
        meta: {
            bodyClas: 'vue--user',
        }
    },
    {
        path: '/themes',
        name: 'themes',
        component: Themes,
        meta: {
            bodyClas: 'vue--themes',
        }
    },
    {
        path: '/app-show',
        name: 'app-show',
        component: AppShow,
        meta: {
            bodyClas: 'vue--app-show',
        }
    },
]

const router = new Router({
    mode: 'history',
    routes
})

router.beforeEach((to, from, next) => {
    window.scrollTo(0, 0);

    if (from.name !== null) {
        NProgress.start();
        document.body.classList.remove(from.meta.bodyClas);
    }
    next();
});

router.afterEach((to) => {
    document.body.classList.add(to.meta.bodyClas);

    NProgress.done();
});

export default router