<template>
  <div class="starter-pro-kit mb-16 relative">
    <div class="container">
      <div class="block-header flex flex-col items-center text-center mb-6 px-10">
        <h2 class="max-w-lg lg:max-w-full text-3xl lg:text-34px text-gray-800 font-medium mb-3">Feeling Lost? Start with our hand-picked apps for you.</h2>
        <p class="max-w-sm lg:max-w-full text-base md:text-lg lg:text-22px text-gray-500 font-light">Dont know where to start? Go with what Salla suggests.</p>
      </div>
    </div>

    <div class="flex justify-center overflow-hidden" v-view="onceCheckportHandler">
      <div class="kit-slider max-w-full lg:max-w-3xl py-10">
        <splide :options="sliderOptions" class="w-full">
          <splide-slide v-for="slide in slides" :key="slide.id" class="bg-white shadow-light rounded-3xl px-5 md:px-10 py-6 md:py-20 flex flex-col md:flex-row justify-between items-center overflow-hidden kit-anime opacity-0">
            <div class="text-center md:text-left mb-5 sm:mb-10 md:mb-0 md:pe-10 max-w-xs md:max-w-full min-w-1/2">
              <h2 class="text-4xl md:text-5xl font-bold text-primary whitespace-nowrap">{{slide.title}}</h2>
              <p class="text-gray-500 font-light mb-6">{{slide.desc}}</p>
              
              <router-link to="/" class="inline-flex items-center text-gray-600 hover:text-primary transition duration-300">
                <span class="fix-font">{{slide.btnText}}</span> 
                <span class="icon sicon-right-arrow ms-3 text-xs"></span>
              </router-link>
            </div>
            <img :src="appPreviewImg" alt="slide.alt" class="max-w-3/4 md:max-w-xs lg:max-w-full -mb-12 sm:-mb-24 md:mb-0 md:-me-16">
          </splide-slide>
        </splide>
      </div>
    </div>

  </div>
</template>

<script>
import { Splide, SplideSlide } from '@splidejs/vue-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';

export default {
  components: {
    Splide,
    SplideSlide,
  },
  data(){
    return {
      showed: false,
      sliderOptions: {
        rewind: true,
        gap   : '4rem',
        arrows: false,
        pagination: false,
        breakpoints: {
          1024: {
            padding: '7rem',
            gap   : '2rem',
          },
          768: {
            padding: '4rem',
            gap   : '2rem',
          },
        }
      },
      slides: [
        {id: 0, title: 'Starter Kit', desc: 'Selected apps for you to begin with. Ease your merchant journey with Salla Apps.', btnText: 'Explore now', btnURL: '/'},
        {id: 1, title: 'Pro Kit', desc: 'Easy access to several paid apps that are curated for your store.', btnText: 'Explore now', btnURL: '/'},
      ],
      appPreviewImg: require('../../../assets/images/apps-preivew.svg'),
    }
  },
  methods:{
    onceCheckportHandler(e){
      if(e.percentTop < 0.7 && !this.showed){
        this.animateItems();
        this.showed = true;
      }
    },

    animateItems(){
      this.$anime
        .timeline()
        .add({
          targets: '.kit-anime',
          opacity: [0,1],
          translateX: [100, 0],
          duration: 600,
          // easing: 'cubicBezier(.5, .05, .1, .3)',
          easing: 'spring(1, 100, 10, 0)',
          delay: function(el, i, l) {
            return i * 125;
          }, 
        })
    },
  }
}
</script>

<style lang="scss">
.starter-pro-kit .splide__track{
  overflow: inherit;
}
</style>