<template>
  <nav :class="{'site-header--transparent' : transparentHeader}" class="site-header flex items-center justify-between flex-wrap bg-transparent py-10 sm:px-0 relative z-10">
    <div class="container">
      <div class="site-header__interior flex items-center flex-shrink-0 justify-between relative">
        <!-- Logo -->
        <router-link :to="{name: 'home'}" class="flex items-center me-5 md:me-12 xl:me-24 site-logo transition duration-500">
          <img :src="logo" class="me-2">
          <h1 class="font-medium text-2xl m-0 ms-1 text-primary tracking-tight whitespace-nowrap"><span class="">Salla</span> <span class="font-extralight">app store</span></h1>
        </router-link>
      
        <!-- Mobile Menu Button -->
        <div class="lg:hidden flex flex-row-reverse header-btns transition duration-500">
          <button class="mobile-nav__toggler transition duration-500 flex items-center ps-3 py-2 focus:outline-none text-gray-600" @click="openMobileNav">
            <svg class="fill-current h-6 w-6 pointer-events-none" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
              <title>Menu</title>
              <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
            </svg>
          </button> 
          <button @click="openSearch=true;" class="transition duration-300 p-3 cursor-pointer text-gray-600 outline-none focus:outline-none"><span class="icon sicon-search text-lg"></span></button>
        </div>

        <!-- Menu & Login Buttons -->
        <div class="w-full hidden lg:flex flex-grow lg:items-center lg:w-auto">
          <div class="text-sm lg:flex-grow">
            <Menu @onOpenSearch="openSearch=true;" :searchIsOpened="openSearch"/>
          </div>

          <div class="site-header__btns">
            <router-link to="/" class="me-10 capitalize hover:text-primary font-light">Login</router-link>

            <button class="bg-primary hover:bg-primary-dark text-white py-3 px-6 rounded-lg transition duration-300 focus:outline-none inline-flex items-center">
              <span class="fix-font">Sign up</span>
            </button>
          </div>
        </div>

        <!-- Search -->
        <search :open="openSearch" @closeSearch="openSearch=false"/>

        <!-- Mobile Menu -->
        <mobile-nav :isOpened="mobileNavToggle" @closeMobileNav="closeMobileNav"/>

      </div>
    </div>
  </nav>
</template>
<script>

import Menu from '../partials/Header/Menu';
import MobileNav from './Header/MobileNav.vue';
import Search from './Header/Search.vue';

export default {
  components: { 
    Menu, 
    MobileNav,
    Search 
  },
  data(){
    return{
      logo: require('../../assets/images/salla-logo.svg'),
      mobileNavToggle: false,
      openSearch: false,
    }
  },
  mounted(){
    // Close Mobile nav on click outside
    const that = this;
    document.addEventListener('click', function(event) {
      const mobileNavContent = document.querySelector('.mobile-nav__content');
      if (!mobileNavContent.contains(event.target) && !event.target.classList.contains('mobile-nav__toggler')) {
        that.mobileNavToggle = false;
        that.$emit('mobileNavIsOpend', false);
      } 
    });
  },
  computed:{
    transparentHeader(){
      return this.$route.name == 'home'
    },
    env(){
      return process.env.NODE_ENV;
    }
  },
  methods:{
    openMobileNav(){
      this.mobileNavToggle = true;
      this.$emit('mobileNavIsOpend', true);
      this.animateMobileNavItems();
    },

    closeMobileNav(){
      this.mobileNavToggle = false;
      this.$emit('mobileNavIsOpend', false);
    },

    animateMobileNavItems(){
      this.$anime
        .timeline()
        .add({
          targets: '.mobile-nav a',
          opacity: [0,1],
          translateY: [20, 0],
          duration: 1200,
          delay: function(el, i, l) {
            return i * 100;
          },
        }, '+=200');
    },
  }
}
</script>

<style lang="scss">
.mobile-nav-is-opened{  
  .header-btns,
  .site-logo{
    // opacity: 0;
    filter: blur(8px);
  }
}
</style>