<template> 
  <kinesis-container class="hero-area mx-auto w-full bg-secondary px-4 pt-56 pb-8 lg:pb-24 mb-10 lg:mb-20 text-white overflow-hidden">

    <div class="container flex-1 flex justify-center items-center flex-col">
      
      <div class="hero-area__content max-w-2xl flex flex-col justify-center items-center text-center mb-20">

        <div class="salla-logo w-32 h-32 rounded-large relative mb-6 pointer-events-none">
          <div class="logo-anime opacity-0 absolute w-full h-full">
            <kinesis-element axis="y" :strength="10" class="bg bg-primary-dark absolute w-full h-full rounded-large z-0">
            </kinesis-element>
          </div>
          <kinesis-element axis="y" :strength="15" class="absolute bottom-0 z-20">
            <img :src="logoFront" class="logo-anime opacity-0" />
          </kinesis-element>
          <kinesis-element axis="y" :strength="7" class="absolute top-3 z-10">
            <img :src="logoSides" class="logo-anime opacity-0" />
          </kinesis-element>

          <div class="w-20 h-12 absolute left-6 bottom-5 z-30">
            <kinesis-element :strength="5" class="absolute top-0 left-0">
              <img :src="sallaEye" class="logo-anime opacity-0 max-w-full">
            </kinesis-element>
            <kinesis-element :strength="8" class="absolute top-0 right-0">
              <img :src="sallaEye" class="logo-anime opacity-0 max-w-full">
            </kinesis-element>
            <kinesis-element :strength="4" class="absolute bottom-0 left-3">
              <img :src="sallaMouth" class="logo-anime opacity-0 max-w-full">
            </kinesis-element>
          </div>
        </div>

        <!-- <img :src="logo" width="128" class="block mb-6"/> -->
        <h2 class="anime-item opacity-0 mb-3 font-bold text-3xl md:text-5xl leading-none tracking-tight">More ways to be <span class="text-primary">you</span></h2>
        <p class="anime-item opacity-0 mx-auto lg:mx-0 max-w-xs mb-2 text-base font-light">Elevate your store performance by using the apps on Salla Marketplace.</p>
      </div>

      <div class="apps-preview flex flex-col justify-center items-center -mb-32">
        <div class="-mt-8 flex" v-for="n in 7" :key="n">
          <template v-if="n > 1">
            <div class="app w-12 h-12 bg-blueGray-500 rounded-lg my-2 mx-10 inline-block bg-center app-anime opacity-0" v-for="i in n" :key="i" :style="{backgroundImage : `url(${smile})`}"></div>
          </template>
        </div>
      </div>

    </div>  

  </kinesis-container>  
</template>

<script>
import { KinesisContainer, KinesisElement} from 'vue-kinesis';

export default {
  components: {
    KinesisContainer,
    KinesisElement
  },
  data(){
    return{
      logo: require('../../../assets/images/salla-logo.svg'),
      logoSides: require('../../../assets/images/salla-logo/sides.svg'),
      logoFront: require('../../../assets/images/salla-logo/front.svg'),

      sallaEye: require('../../../assets/images/salla-logo/eye.svg'),
      sallaMouth: require('../../../assets/images/salla-logo/mouth.svg'),

      smile: require('../../../assets/images/smile.svg'),
    }
  },
  mounted(){
    this.$nextTick(()=>{
      // setTimeout(()=>{
        this.animateItems();
      // }, 2000)
    })
  },
  methods:{
    animateItems(){
      this.$anime
        .timeline()
        .add({
          targets: '.logo-anime',
          opacity: [0,1],
          translateY: [40, 0],
          duration: 1000,
          easing: 'spring(1, 80, 8, 0)',
          delay: function(el, i, l) {
            return i * 100;
          },
        }, '+=800')
        .add({
          targets: '.hero-area__content .anime-item',
          opacity: [0,1],
          translateY: [20, 0],
          duration: 1200,
          easing: 'spring(1, 80, 10, 0)',
          delay: function(el, i, l) {
            return i * 100;
          },
        }, '-=1800')
        .add({
          targets: '.app-anime',
          opacity: [0,1],
          translateY: [70, 0],
          easing: 'spring(0.8, 60, 9, 1)',
          delay: function(el, i, l) {
            return i * 50;
          },
        }, '-=1800')
    },
  }
}
</script>