<template>
  <ul class="site-header__menu">
    <li v-for="(item, index) in menuItems" :key="index" class="inline-block opacity-0"> 
      <router-link :to="item.path" class="main-menu-item block transition duration-300 mx-2 p-2 xl:p-4 hover:text-primary hover:text-gradient hover:bg-gradient-to-r hover:from-primary hover:to-gradient-second-color text-base">
        {{item.text}}
      </router-link>
    </li>

    <li class="inline-block search-item ms-4 opacity-0">
      <button @click="openSearch" class="transition duration-300 mx-2 p-4 cursor-pointer text-gray-500 hover:text-primary outline-none focus:outline-none"><span class="icon sicon-search text-base"></span></button>
    </li> 
  </ul>
</template>

<script>
export default {
  props: ['searchIsOpened'],
  data(){
    return{
      menuItems: [
        {text: 'Categories', path: '/categories'},
        {text: 'Themes', path: '/themes'},
      ],
    }
  },

  watch:{
    searchIsOpened(val){
      if(val==true){
        this.openSearch()
      }
    }
  },

  mounted(){
    this.animateItems();
  },

  methods:{
    animateItems(){
      this.$anime
        .timeline()
        .add({
          targets: '.site-header__menu > li',
          opacity: [0,1],
          translateY: [20, 0],
          delay: function(el, i, l) {
            return i * 50;
          },
        }, '+=700');
    },

    openSearch(){
      this.$emit('onOpenSearch');

      this.$anime
        .timeline()
        .add({
          targets: '.site-header__menu > li',
          opacity: [1,0],
          translateY: [0, -20],
          delay: function(el, i, l) {
            return i * 50;
          },
        })
        .add({
          targets: '.search-box .anime-item',
          opacity: [0,1],
          translateY: [30, 0],
          delay: function(el, i, l) {
            return i * 75;
          }
        }, '-=900');

        document.querySelector('.search-box input').focus();
    },
  }
}
</script>

<style>
  @media (max-width: 1100px){
    .main-menu-item{
      padding: 8px 4px;
    }
  }
</style>