<template>
  <div class="featured-cats mb-20 relative" v-view="onceCheckportHandler">
    <div class="w-8 sm:w-12 h-full absolute bottom-0 left-0 bg-gradient-to-r from-white to-transparent z-10 pointer-events-none"></div>

    <img :src="dotsBg" class="absolute -top-32 -left-12 -z-1 opacity-60 md:opacity-100"/>
    <div class="container p-0 lg:p-5">
      <h2 class="text-3xl lg:text-34px font-medium text-center mb-8 md:mb-16 cat-item-anime opacity-0">Featured Categories</h2>
      
      <!-- <ul class="featured-cats__list flex list-none flex-1 justify-around w-full lg:px-12 flex-wrap">
        <li v-for="item in cats" :key="item.id" class="w-1/2 sm:w-1/3 lg:w-1/5 text-center mb-10">
          <router-link to="/" class="inline-flex flex-col justify-center items-center text-primary-dark hover:text-primary transition duration-300">
            <span :class="`sicon-${item.icon}`" class="icon inline-flex justify-center items-center w-28 h-28 text-primary bg-teal-50 rounded-3xl text-3xl mb-5"></span>
            <span class="font-bold">{{item.text}}</span>
          </router-link>
        </li>
      </ul> -->

      <splide :options="sliderOptions" class="w-full">
        <splide-slide v-for="item in cats" :key="item.id" class="w-1/2 sm:w-1/3 lg:w-1/5 text-center mb-10">
          <router-link to="/" class="cat-link inline-flex flex-col justify-center items-center text-primary-dark hover:text-primary transition duration-300">
            <span :class="`sicon-${item.icon}`" class="icon inline-flex justify-center items-center w-28 h-28 text-primary bg-teal-50 rounded-3xl text-3xl mb-5 cat-item-anime opacity-0 relative overflow-hidden">
              <span class="hover-overlay w-24 h-96 bg-white bg-opacity-50 absolute -left-10 bottom-32 transform translate-y-full transition duration-500 -rotate-45 ease-linear"></span>
            </span>
            <span class="font-bold cat-item-anime opacity-0">{{item.text}}</span>
          </router-link>
        </splide-slide>
      </splide>
    </div>

    <div class="w-8 sm:w-12 h-full absolute bottom-0 right-0 bg-gradient-to-l from-white to-transparent z-10 pointer-events-none"></div>
  </div>
</template>

<script>
import { Splide, SplideSlide } from '@splidejs/vue-splide';

export default {
  components: {
    Splide,
    SplideSlide,
  },
  data(){
    return{
      showed: false,
      sliderOptions: {
        rewind: true,
        gap   : '4rem',
        perPage: 5,
        arrows: false,
        pagination: false,
        breakpoints: {
          1024: {
            padding: '7rem',
            gap   : '2rem',
            perPage: 4,
          },
          992: {
            padding: '10rem',
            gap   : '2rem',
            perPage: 3,
          },
          768: {
            padding: '5rem',
            gap   : '2rem',
            perPage: 3,
          },
          578: {
            padding: '7rem',
            gap   : '2rem',
            perPage: 2,
          },
          460: {
            padding: '4rem',
            gap   : '2rem',
            perPage: 2,
          },
          380: {
            padding: '7rem',
            gap   : '2rem',
            perPage: 1,
          },
        }
      },
      cats: [
        {id: 0, icon: 'support', text: 'Customer Support', url:'/'},
        {id: 1, icon: 'optimize', text: 'Site Optimization', url:'/'},
        {id: 2, icon: 'shipping', text: 'Shipping & Fulfillment', url:'/'},
        {id: 3, icon: 'security', text: 'Security', url:'/'},
        {id: 4, icon: 'accounting', text: 'Accounting & Finance', url:'/'},
      ],
      dotsBg: require('../../../assets/images/dots-bg.svg'),
    }
  },

  methods:{
    onceCheckportHandler(e){
      if(e.percentTop < 0.8 && !this.showed){
        this.animateItems();
        this.showed = true;
      }
    },

    animateItems(){
      this.$anime
        .timeline()
        .add({
          targets: '.cat-item-anime',
          opacity: [0,1],
          translateY: [20, 0],
          duration: 1000,
          delay: function(el, i, l) {
            return i * 75;
          }, 
        })
    },
  }
}
</script>

<style lang="scss" scoped>
.cat-link:hover{
  .hover-overlay{
    transform: translate3d(100%,-200px,0) rotate(-45deg);
  }
}
