    CategoriesNav,
    MostPopular,
    SearchApps,
    AppItem,
    AppItemPlaceholder,
    vueStickySidebar,
  },
  data() {
    return {
      isLoading: true,
      apps: [
        { id: 0, color: "orange" },
        { id: 1, color: "blue" },
        { id: 2, color: "green" },
        { id: 3, color: "teal" },
        { id: 4, color: "red" },
        { id: 5, color: "indigo" },
        { id: 6, color: "pink" },
        { id: 7, color: "purple" },
        { id: 8, color: "green" },
        { id: 9, color: "teal" },
        { id: 10, color: "red" },
        { id: 11, color: "indigo" },
        { id: 12, color: "pink" },
        { id: 13, color: "purple" },
        { id: 14, color: "green" },
        { id: 15, color: "teal" },
        { id: 16, color: "red" },
        { id: 17, color: "indigo" },
      ],
      cats: [
        { id: 10, text: "All Categories" },
        { id: 11, text: "Featured" },
        { id: 22, text: "Analytics" },
        { id: 33, text: "CRM" },
        { id: 44, text: "Collaborations" },
        { id: 55, text: "Customer Services" },
        { id: 66, text: "Sales" },
        { id: 77, text: "Shipping" },
      ],
      currentCat: { id: 0, text: "All Categories" },
      showed: false,
    };
  },
  mounted() {
    this.semulateFetch();
  },
  methods: {
    // onceCheckportHandler(e){
    //   if(e.percentTop < 0.9 && !this.showed){
    //     this.animateItems();
    //     this.showed = true;
    //   }
    // },

    semulateFetch() {
      setTimeout(() => {
        this.isLoading = false;
        this.$nextTick(() => {
          this.animateItems();
        });
      }, 2000);
    },

    onSelectedCatChanged(item) {
      this.filter(item);
    },

    animateItems() {
      this.$anime.timeline().add({
        targets: ".app-item-anim",
        opacity: [0, 1],
        translateY: [20, 0],
        duration: 1200,
        delay: function (el, i, l) {
          return i * 100;
        },
      });
    },

    filter(cat) {
      this.isLoading = true;
      this.currentCat = cat;
      this.semulateFetch();
    },
  },
};
</script>

