<template>
  <div class="search-box search-box--tutorial mt-16 mb-6 relative w-full md:w-1/2 mx-auto" :class="{ 'is-opend' : open, 'is-searching' : isSearching}">
    
    <div class="search-bar relative bg-white rounded-md h-16 w-full shadow-huge ">
      <span class="spinner-loader-wrap w-4 h-4 absolute">
        <span class="block spinner-loader w-4 h-4 animate-spin border-2 border-gray-300 rounded-full"></span>
      </span>

      <span class="search-icon absolute w-5 h-5 flex justify-center"><span class="sicon-search text-xl text-gray-400"></span></span>

      <input 
        @input="debounceSearch"
        ref="tutorialSearchInput"
        class="w-full h-16 outline-none px-12 pb-1 rounded-md bg-transparent" 
        placeholder="Search Apps..."
      />

      <button v-show="search_term" @click="closeSearch" class="text-xl absolute right-2 top-5 text-gray-500 outline-none focus:outline-none hover:text-gray-600 transition duration-300 w-8 fix-font"><span class="sicon-close inline-block"></span></button>
    </div>

    <div class="search-results bg-white rounded-md w-full shadow-2xl mt-3 z-20 absolute p-5" v-if="searchResults">
      <span v-if="searchResults.length == 0" class="text-gray-500 font-light">There are no Apps for your search..</span>
      <div v-else>
        <div class="flex justify-between items-center mb-3">
          <span class="text-sm text-gray-500">Search Apps</span>
          <span class="text-xs text-gray-500 font-light" v-if="searchResults">({{searchResults.length}} Results)</span>
        </div>
        <ul class="max-h-64 md:max-h-92 overflow-y-auto pe-1 divide-y divide-gray-200">
          <li v-for="(item,n) in searchResults" :key="n">
            <router-link :to="getPathName(item.url)" class="block py-3 text-gray-700 hover:text-primary transition duration-300 xt-base font-light">{{item.title}}</router-link>
          </li> 
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios';
import SETTINGS from '../../../settings';

export default {
  props: ['open'],
  data(){
    return{
      search_term: '',
      isSearching: false,
      searchResults: null,
      per_page: 100,
    }
  },

  watch:{
    search_term(val){
      if(val.length > 0){
        this.isSearching = true;
        this.fetchResults();
      }
      else{
        this.searchResults = null;
      }
    }
  },

  methods:{
    debounceSearch(event) {
      this.typing = true;
      clearTimeout(this.debounce)
      this.debounce = setTimeout(() => {
        this.typing = false
        this.search_term = event.target.value
      }, 500)
    },

    fetchResults(){
      this.searchResults = null;
      axios
      .get(SETTINGS.API_BASE_PATH + 'search?subtype=tutorial&search=' + this.search_term + '&per_page=' + this.per_page)
      .then(response => {
        this.searchResults = response.data;
        this.isSearching = false;
      })
      .catch(e => {
        console.log(e);
      });
    },

    closeSearch(){
      this.searchResults = null;
      this.search_term = '';
      this.$refs['tutorialSearchInput'].value = '';
    },

    getPathName(href){
      var l = document.createElement("a");
      l.href = href;
      return l.pathname; 
    }
  }
}
</script>