    animateItems() {
      this.$anime.timeline().add({
        targets: ".app-item-anim",
        opacity: [0, 1],
        translateY: [20, 0],
        duration: 1200,
        delay: function (el, i, l) {
          return i * 100;
        },
      });
    },

    filter(cat) {
      this.isLoading = true;
      this.currentCat = cat;
      this.semulateFetch();
    },
  },
