<template>
  <div class="search-box search-box--header inset-center-x absolute top-1 w-1/2" :class="{'pointer-events-none' : !open , 'is-opend' : open, 'is-searching' : isSearching}">
    <div class="search-bar relative bg-white rounded-md h-12 w-full shadow-2xl anime-item opacity-0">
    
      <span class="spinner-loader-wrap w-4 h-4 absolute">
        <span class="block spinner-loader w-4 h-4 animate-spin border-2 border-gray-300 rounded-full"></span>
      </span>

      <span class="search-icon absolute w-5 h-5 flex justify-center items-center"><span class="sicon-search text-xl text-gray-400 z-10 anime-item opacity-0"></span></span>

      <input 
        @input="debounceSearch"
        ref="siteSearchInput"
        class="w-full h-12 outline-none px-12 rounded-md bg-transparent anime-item opacity-0" placeholder="Search Site..."
      />

      <button @click="closeSearch" class="text-xl absolute right-2 top-3 text-gray-500 outline-none focus:outline-none hover:text-gray-600 z-10 transition duration-300 w-8 fix-font"><span class="sicon-close anime-item opacity-0 inline-block"></span></button>
    </div>

    <div class="search-results bg-white rounded-md w-full shadow-2xl mt-3 z-20 relative p-5" v-if="searchResults && open">
      <span v-if="searchResults.length == 0" class="text-gray-500 font-light">There are no results for your search..</span>
      <div v-else>
        <div class="flex justify-between items-center mb-3">
          <span class="text-sm text-gray-500">Search Results</span>
          <span class="text-xs text-gray-500 font-light" v-if="searchResults">({{searchResults.length}} Results)</span>
        </div>
        <ul class="max-h-64 md:max-h-92 overflow-y-auto pe-1 divide-y divide-gray-200">
          <li v-for="(item,n) in searchResults" :key="n">
            <router-link @click.native="closeSearch" :to="getPathName(item.url)" class="flex justify-between py-3 text-gray-700 hover:text-primary transition duration-300 xt-base font-light">
              <span>{{item.title}}</span>
              <span :class="`ms-5 mb-auto pt-1 pb-2 px-2 rounded-md leading-none text-sm text-${searchItemColor(item.subtype)}-500 bg-${searchItemColor(item.subtype)}-100`">{{item.subtype}}</span>
            </router-link>
          </li> 
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios';
import SETTINGS from '../../../settings';

export default {
  props: ['open'],
  data(){
    return{
      search_term: '',
      isSearching: false,
      searchResults: null,
      per_page: 100,
      debounce: null,
    }
  },

  mounted(){
    const that = this;
    document.onkeydown = function(evt) {
      evt = evt || window.event;
      var isEscape = false;
      if ("key" in evt) {
          isEscape = (evt.key === "Escape" || evt.key === "Esc");
      } else {
          isEscape = (evt.keyCode === 27);
      }

      if (isEscape && that.open) {
        that.closeSearch();
      }
    }
  },

  watch:{
    search_term(val){
      if(val.length > 0){
        this.isSearching = true;
        this.fetchResults();
      }
      else{
        this.searchResults = null;
      }
    }
  },

  methods:{
    searchItemColor(type){
      switch(type){
        case 'post':
          return 'green';
        case 'tutorial':
          return 'red';
        case 'event':
          return 'orange';
        case 'page':
          return 'blue';
        default: 
          return 'gray';
      }
    },

    closeSearch(){
      this.$emit('closeSearch');
      this.searchResults = null;
      this.search_term = '';
      this.$refs['siteSearchInput'].value = '';
      
      this.$anime
        .timeline()
        .add({
          targets: '.search-box .anime-item',
          opacity: [1,0],
          translateY: [0, -30],
          delay: function(el, i, l) {
            return i * 75;
          },
        })
        .add({
          targets: '.site-header__menu > li',
          opacity: [0,1],
          translateY: [20, 0],
          delay: function(el, i, l) {
            return i * 50;
          },
        }, '-=900')
    },

    debounceSearch(event) {
      this.typing = true;
      clearTimeout(this.debounce)
      this.debounce = setTimeout(() => {
        this.typing = false
        this.search_term = event.target.value
      }, 500)
    },

    fetchResults(){
      this.searchResults = null;
      axios
      .get(SETTINGS.API_BASE_PATH + 'search?search=' + this.search_term + '&per_page=' + this.per_page)
      .then(response => {

        console.log('response:: ', response);

        this.searchResults = response.data;
        this.isSearching = false;
      })
      .catch(e => {
        console.log(e);
      });
    },

    getPathName(href){
      var l = document.createElement("a");
      l.href = href;
      return l.pathname; 
    }
  }
}
</script>