<template>
  <div>
    <Header @mobileNavIsOpend="mobileNavIsOpend"/>

    <div class="site-container" :class="{'is-blured' : mobileNavToggle}">
      <div class="site-content w-full mx-auto flex-1 md:px-0">
        <transition
          name="fade"
          mode="out-in"
          @beforeLeave="beforeLeave"
          @afterEnter="afterEnter"
        >
          <router-view :key="key"/>
        </transition>
      </div>
    </div>

    <Footer />
  </div>
</template>


<script>
// import { mapActions } from 'vuex'
import Header from './components/partials/Header.vue';
import Footer from './components/partials/Footer.vue';

export default {
  components: { 
    Header,
    Footer,
  },
  data() {
    return {
      loading: false,
      mobileNavToggle: false,
    };
  },
  computed: {
    key() {
      return this.$route.fullPath;
    },
  },

  methods:{
    beforeLeave() {
      this.loading = true;
    },
    afterEnter() { 
      this.loading = false;
    },
    mobileNavIsOpend(toggler){
      this.mobileNavToggle = toggler;
    }
  }
}
</script>

<style lang="scss">
.site-container{
  transition: filter 0.3s;
}

.is-blured{
  filter: blur(8px);
}

.fade-enter-active,
.fade-enter{
	transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
}

.fade-leave-active {
	transform: translateY(0);
}

.fade-enter,
.fade-leave-active {
	opacity: 0;
	transform: translateY(20px);
}

</style>