<template>
    <div class="site-mapper mb-20">
        <div>
            <div class="lg:w-4/5 relative overflow-hidden transition duration-700 text-darker">
              <h4 class="text-lg font-semibold">About SiteMapper</h4>
              <div v-html="limitTo(appDesc)"></div>

              <div v-if="!expanded" class="w-full h-32 absolute bottom-0 right-0 bg-gradient-to-t from-white to-transparent z-10 pointer-events-none"></div>
            </div>
            <div class="lg:w-4/5 text-center">
              <button @click="expanded = !expanded" :class="{'mt-3': expanded}" class="text-primary focus:outline-none font-medium transition hover:text-primary-dark">
                {{expanded? 'Read Less' : 'Read More'}}
              </button>
            </div>
        </div>
    </div>
</template>

<script>
