<template>
  <div class="mb-20">
    <div class="container">
      <h2 class="text-3xl lg:text-34px font-medium text-center mb-8 md:mb-16 app-item-anim opacity-0">Most Popular</h2>
    
      <div class="apps-list grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8 xl:gap-12" v-view="onceCheckportHandler">
        <app-item v-for="app in apps" :key="app.id" :color="app.color"/>
      </div>
    </div>
  </div>
</template>

<script>
import AppItem from '../AppItem.vue'

export default {
  components: {
    AppItem
  },
  data(){
    return{
      showed: false,
      apps: [
        {id: 0, color: 'orange'},
        {id: 1, color: 'blue'},
        {id: 2, color: 'green'},
        {id: 3, color: 'teal'},
        {id: 4, color: 'red'},
        {id: 5, color: 'indigo'},
        {id: 6, color: 'pink'},
        {id: 7, color: 'purple'},
      ]
    }
  },
  methods:{
    onceCheckportHandler(e){
      if(e.percentTop < 0.8 && !this.showed){
        this.animateItems();
        this.showed = true;
      }
    },

    animateItems(){
      this.$anime
        .timeline()
        .add({
          targets: '.app-item-anim',
          opacity: [0,1],
          translateY: [20, 0],
          duration: 1200,
          delay: function(el, i, l) {
            return i * 100;
          }, 
        })
    },
  }
}
</script>App