<template>
  <div class="container">
    <kinesis-container v-view="onceHandler" class="join-community mx-auto w-full bg-secondary pt-4 lg:pt-24 pb-10 lg:pb-24 text-white overflow-x-hidden mt-12 px-4 rounded-3xl">
      <div class="flex-1 flex flex-col md:flex-row justify-between items-center pt-10 lg:pt-0 px-10 md:px-32">
        <div class="join-community__content text-center md:text-left order-2 lg:order-1">
          <h2 class="anime-item opacity-0 mb-3 text-white font-bold text-4xl sm:text-34px pb-2 tracking-tight leading-10">Excited to make <br><span class="text-primary">Your</span> own app?</h2>
          <p class="anime-item opacity-0 max-w-xs mb-2 font-light">Be part of the Salla Developer community</p>

          <div class="mt-8 flex items-center flex-col md:flex-row">

            <span class="anime-item opacity-0 inline-block">
              <a href="https://salladevelopers.slack.com" target="_blank" class="bg-primary hover:bg-primary-dark text-white py-3 px-8 rounded-lg transition duration-300 focus:outline-none inline-flex items-center">
                <span class="fix-font">Join us</span>
              </a>
            </span>
          </div>
        </div>

        <div class="salla-smile w-1/2 h-56 relative order-1 md:order-2 mb-8 lg:mb-0">
          <kinesis-element :strength="15" class="eye-right absolute">
            <img :src="sallaEye" class="smile-anime opacity-0 max-w-full">
          </kinesis-element>
          <kinesis-element :strength="25" class="eye-left absolute">
            <img :src="sallaEye" class="smile-anime opacity-0 max-w-full">
          </kinesis-element>
          <kinesis-element :strength="4" class="mouth absolute">
            <img :src="sallaMouth" class="smile-anime opacity-0 max-w-full">
          </kinesis-element>
        </div>
      </div>  

    </kinesis-container>  
  </div>
</template>

<script>
import { KinesisContainer, KinesisElement } from 'vue-kinesis'

export default {
  components: { 
    KinesisContainer,
    KinesisElement
  },
  data(){
    return{
      showed: false,
      sallaEye: require('../../assets/images/salla-eye.svg'),
      sallaMouth: require('../../assets/images/salla-mouth.svg'),
    }
  },

  mounted(){
  
  },

  methods:{
    animateItems(){
      this.$anime
        .timeline()
        .add({
          targets: '.join-community__content .anime-item',
          opacity: [0,1],
          translateY: [20, 0],
          duration: 1200,
          delay: function(el, i, l) {
            return i * 100;
          }, 
        })
        .add({
          targets: '.salla-smile .smile-anime',
          scale: [0, 1],
          opacity: [0, 1],
          translateY: [20, 0],
          translateX: [20, 0],
          delay: function(el, i, l) {
            return i * 100;
          },
        }, '-=1200')
    },

    onceHandler(e){
      if(e.percentTop < 0.8 && !this.showed){
        this.animateItems();
        this.showed = true;
      }
    },
  }
}
</script>

<style lang="scss" scoped>
  .salla-smile{
    width: 300px;
    height: 230px;

    @media (max-width: 992px){
     width: 140px;
     height: 111px;
    }

    .eye-right{
      bottom: 70%;
      right: 1%;
      width: 27%;
    }

    .eye-left{
      bottom: 24%;
      width: 27%;
    }

    .mouth{
      bottom: 0;
      right: 0;
      width: 75%;
    }
  }
</style>